.w {
    margin-top: 2em;
    margin-left: 2rem;
    margin-right: 2rem;
    margin-bottom: 10vh;
}

@media only screen and (min-width: 900px) {
    .w {
        width: 70vw;
        margin: auto;
        margin-bottom: 10vh;
        margin-top: 2em;
    }
    ;
}

blockquote {
    background: #323742;
    border-left: 10px solid #1e2631;
    margin: 1.5em 10px;
    padding: 1em 10px .1em 10px;
    quotes: "\201C""\201D""\2018""\2019";
}

.gh-button {
    display: inline-block;
}

.gh-button {
    box-sizing: border-box;
    height: 30px;
    font-weight: bold;
    font-size: 11px;
    line-height: 14px;
    padding: 2px 5px 2px 4px;
    color: #333;
    text-decoration: none;
    text-shadow: 0 1px 0 #fff;
    white-space: nowrap;
    cursor: pointer;
    overflow: hidden;
    border-radius: 3px;

    padding: 3px 10px 3px 8px;
    font-size: 16px;
    line-height: 22px;
    border-radius: 4px;
    background-color: #eee;
    background-image: linear-gradient(#fcfcfc, #eee);
    border: 1px solid #d5d5d5;
}

.gh-button:hover,
.gh-button:focus {
    text-decoration: none;
    background-color: #ddd;
    background-image: linear-gradient(#eee, #ddd);
    border-color: #ccc;
}

.gh-button:active {
    background: #dcdcdc;
    border-color: #b5b5b5;
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.gh-button__icon {
    display: inline-block;
    vertical-align: top;
    width: 20px;
    height: 20px;
    margin-right: 4px;
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
}